
*{margin: 0;padding: 0;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
a {
  display: block;
  color: #666;
}
::-webkit-scrollbar {
    width:0px;
}
.router-link-active{color: #42b983;
}
.cfx:after{content:"";display: block;clear: both;
}
.fl{float: left;
}
.fr{float: right;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body{
  max-width: 800px;
  margin: 0 auto;
}



