
.news-view{
  position: relative;
  margin: 48px 0;
  text-align: left;
}
.news-view-list{
  margin: 10px 10px;
  padding: 15px 0;
  box-shadow: 0px 2px 1px #eee;
  background-color: #fbfbfb;
  font-size: 12px;
  border-radius: 5px;
}
/*内容*/
.news-view-detail .markdown-text{
  padding-bottom: 20px;
}
.news-view-detail{
  position: fixed;
  left: 0;
  top: 40px;
  bottom: 40px;
  background-color: #fff;
  width: 92%;
  height: 90%;
  overflow-y: auto;
  padding: 0 4%;
  padding-bottom: 0px;
  word-wrap:break-word;
  word-break:normal;
  font-size: 14px;
}
@media screen and (min-width: 900px){
.news-view-detail{
    max-width: 800px;
    left: 50%;
    margin-left: -400px;
    padding: 0;
}
}
.news-view-detail img{
  width: 98%;
}
.news-view-detail table{
  max-width: 98%;
  zoom: 0.8;
  padding: 0;
  border-collapse: collapse;
  border-spacing: 2px;
  border: 1px solid #999;
  cellpadding: 10;
  cellspacing: 2px;
}
.news-view-detail h1{
  font-size: 22px;
  padding: 10px 0;
}
.news-view-detail h2{
  font-size: 20px;
  padding: 10px 0;
}
.news-view-detail h3{
  font-size: 18px;
  padding: 10px 0;
}
.news-view-detail p{
  text-indent: 20px;
  padding: 3px 0;
}
.news-view-detail a{
  color: #42b983;
}
.news-view-detail ul, .news-view-detail ol{
  list-style: disc;
  padding: 20px 0;
  padding-left: 20px;
}
.news-view-detail ul li, .news-view-detail ol li{
  padding: 5px 0;
  font-size: 12px;
  color: #666;
}
.news-view-detail pre{
  display: block;
  overflow: auto;
  background-color: #eee;
  word-wrap:break-word;
}
.news-view-detail code{
  display: block;
  overflow: auto;
  word-wrap:break-word;
}
.news-view-detail pre, .news-view-detail code{
  padding: 10px;
  font-family: 'Microsoft Yahei';
}
.news-view-back{
  position: fixed;
  top: 8px;
  right: 4%;
  padding: 3px 5px;
  z-index: 2;
  font-size: 12px;
  color: #fff;
  border: 0px solid #bbb;
  border-radius: 5px;
  background-color: #42b983;
}
@media screen and (min-width: 900px){
.news-view-back{
    right: 50%;
    margin-right: -380px;
}
}
/*内容*/
.news-view-list-left{
  width: 10%;
  text-align: center;
}
.news-view-list-right{
  margin-top: 8px;
  width: 90%;
}
.author-avatar img{
  max-width: 30px;
  height: auto;
  border-radius: 3px;
}
.nvlr-left{
  width: 8%;
  margin-right: 2%;
}
.nvlr-middle{
  width: 70%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  color: #222;
}
.nvlr-right{
  width: 15%;
  text-align: right;
  color: #999;
  margin-right: 5px;
}
.tab-top, .tab-good, .tab-share, .tab-ask{
  color: #fff;
  border-radius: 3px;
  text-align: center;
}
.tab-top{
  background-color: #f00;
}
.tab-good{
  background-color: #FF7F24;
}
.tab-share{
  background-color: #42b983;
}
.tab-ask{
  background-color: #999;
}



