
.foot{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40px;
  background-color: #fff;
  color: #fff;
  z-index: 99;
  box-shadow: 0px -1px 1px #eee;
}
@media screen and (min-width: 900px){
.foot{
    max-width: 800px;
    left: 50%;
    margin-left: -400px;
}
}
.nav{
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.nav li{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  vertical-align: middle;
}
.nav li a{
  text-decoration: none;
  display: block;
  height: 40px;
  line-height: 40px;
}
.nav-list{text-align: center;
}
.nav-list img{width: 26px;margin-top: 6px;
}



